<template>
    <div id="si-main">
        <dv-full-screen-container style="min-height:635 !import">
            <div class="main-container">
                <div class="main-container-left">
                    <dv-border-box-2 :color="['#0B1D71', '#7492DD']" class="main-container-left-top">
                        <div class="main-container-left-top-tab">
                            <div class="menu">CAN数据</div>
                            <div></div>
                        </div>
                        <div class="main-container-left-top-context">
                            <CellGroup>
                                <Cell title="当前车速" extra="100" />
                                <Cell title="当前车速" extra="100" />
                                <Cell title="当前车速" extra="100" />
                                <Cell title="当前车速" extra="100" />
                                <Cell title="当前车速" extra="100" />
                                <Cell title="当前车速" extra="100" />
                                <Cell title="当前车速" extra="100" />
                                <Cell title="当前车速" extra="100" />
                                <Cell title="当前车速" extra="100" />
                                <Cell title="当前车速" extra="100" />
                                <Cell title="当前车速" extra="100" />
                            </CellGroup>
                        </div>
                    </dv-border-box-2>
                    <dv-border-box-2 :color="['#0B1D71', '#7492DD']" class="main-container-left-bottom">
                        <dv-scroll-board :config="config" carousel="page" style="height:320px;" />
                    </dv-border-box-2>
                </div>
                <div class="main-container-center">
                    <dv-border-box-11 class="main-container-center-top" title="历史轨迹回放">
                        <div id="container" class="container"></div>
                    </dv-border-box-11>
                    <dv-border-box-2 :color="['#0B1D71', '#7492DD']" class="main-container-center-bottom">
                        <Row style="padding:20px;">
                            <Col span="2">
                            <!-- <Icon type="md-square" /> -->
                            <Button ghost :icon="ifplaybtn==true?'md-square':'ios-play'" size="small" shape="circle" @click="onPlayClick"></Button>
                            </Col>
                            <Col span="22">
                            <Progress :percent="progress" :stroke-color="['#6F8DD6','#283F74' ]" />
                            </Col>
                        </Row>
                    </dv-border-box-2>
                </div>
                <div class="main-container-toolbar">
                    <ButtonGroup vertical>
                        <Button icon="md-videocam" type="primary" @click="onvideoclick"></Button>
                        <Button icon="md-analytics" type="primary"></Button>
                    </ButtonGroup>
                </div>
                <div class="main-videodialog">
                    <Modal v-model="videobtn" footer-hide :draggable="true" :mask-closable="false" :width="650">
                        <p slot="header" style="color:#ffffff">
                            <Icon type="ios-film-outline"></Icon>
                            历史视频回放
                            <span style="float:right;margin-right:45px;"> 总时长： {{ duration }} ； 已播放：{{ currentTime }}</span>
                        </p>
                        <video id="videoElement" controls :autoplay="false" width="100%" height="270">您的浏览器不支持HTML5播放，请更换浏览器！</video>
                    </Modal>
                </div>
            </div>
        </dv-full-screen-container>
    </div>
</template>

<script>
    import MapLoader from "@/plugins/amap.js";
    import flvjs from "flv.js/dist/flv";
    export default {
        data() {
            return {
                currentTime: 0, //当前播放 时长 
                duration: 0, //总时长
                progress: 0, // 进度
                videourl: "http://fupoaiwo.github.io/video.flv",
                // flvPlayer: null,
                ifplay: false,
                ifplaybtn: false,
                flvPlayer: null,
                videoElement: null,
                num: 0,
                map: null,
                marker: [],
                lineArr: [],
                polyline: null,
                passedPolyline: null,
                videobtn: false,
                config: {
                    headerBGC: "#0B1D71",
                    oddRowBGC: "#335189",
                    header: ["开始时间", "原始CAN报文"],
                    data: [
                        ["2020/01/19 16:27:01", "AAABBBBBBBBBBCCCCCCCCCCCCCCCCCCCCCCCCC"],
                        ["2020/01/19 16:27:02", "AAABBBBBBBBBBCCCCCCCCCCCCCCCCCCCCCCCCC"],
                        ["2020/01/19 16:27:03", "AAABBBBBBBBBBCCCCCCCCCCCCCCCCCCCCCCCCC"],
                        ["2020/01/19 16:27:04", "AAABBBBBBBBBBCCCCCCCCCCCCCCCCCCCCCCCCC"],
                        ["2020/01/19 16:27:05", "AAABBBBBBBBBBCCCCCCCCCCCCCCCCCCCCCCCCC"],
                        ["2020/01/19 16:27:06", "AAABBBBBBBBBBCCCCCCCCCCCCCCCCCCCCCCCCC"],
                        ["2020/01/19 16:27:07", "AAABBBBBBBBBBCCCCCCCCCCCCCCCCCCCCCCCCC"],
                        ["2020/01/19 16:27:08", "AAABBBBBBBBBBCCCCCCCCCCCCCCCCCCCCCCCCC"],
                        ["2020/01/19 16:27:09", "AAABBBBBBBBBBCCCCCCCCCCCCCCCCCCCCCCCCC"],
                        ["2020/01/19 16:27:10", "AAABBBBBBBBBBCCCCCCCCCCCCCCCCCCCCCCCCC"]
                    ],
                    index: true,
                    columnWidth: [50],
                    align: ["center"]
                }
            };
        },
        methods: {
            onvideoclick() {
                this.videobtn = !this.videobtn;
            },
            loadFlvPlear() {
                let that = this;
                that.videoElement = document.getElementById("videoElement");
                that.flvPlayer = flvjs.createPlayer({
                    type: "flv",
                    url: that.videourl
                });
                that.flvPlayer.attachMediaElement(videoElement);
                that.flvPlayer.load();
                // that.flvPlayer.play();
            },
            onPlayClick() {
                this.videobtn = true;
                if (this.num == 0) {
                    this.loadhistoryPoly();
                    this.num = 1;
                } else {
                    this.ifplaybtn = !this.ifplaybtn;
                }
                
                if (!this.ifplay) {
                    // this.loadFlvPlear();
                    this.loadhistoryPoly();
                    this.ifplay = true;
                }
                if (this.ifplaybtn && this.num ==1) {
                    this.startAnimation();
                }
                if (this.ifplaybtn) {
                    this.num = this.num +1;
                    this.flvPlayer.play();
                    this.resumeAnimation();
                } else {
                    this.flvPlayer.pause();
                    this.pauseAnimation();
                }
                // this.updateData();
            },
            updateData() {
                this.duration = this.videoElement.duration;
                this.currentTime = this.videoElement.currentTime;
                this.progress = this.currentTime * 100 / this.duration;
            },
            loadhistoryPoly() {
                this.lineArr = [
                    [116.478935, 39.997761],
                    [116.478939, 39.997825],
                    [116.478912, 39.998549],
                    [116.478912, 39.998549],
                    [116.478998, 39.998555],
                    [116.478998, 39.998555],
                    [116.479282, 39.99856],
                    [116.479658, 39.998528],
                    [116.480151, 39.998453],
                    [116.480784, 39.998302],
                    [116.480784, 39.998302],
                    [116.481149, 39.998184],
                    [116.481573, 39.997997],
                    [116.481863, 39.997846],
                    [116.482072, 39.997718],
                    [116.482362, 39.997718],
                    [116.483633, 39.998935],
                    [116.48367, 39.998968],
                    [116.484648, 39.999861]
                ];
                let that = this;
                that.marker = new AMap.Marker({
                    map: that.map,
                    position: [116.478935, 39.997761],
                    icon: "https://webapi.amap.com/images/car.png",
                    offset: new AMap.Pixel(-26, -13),
                    autoRotation: true,
                    angle: -90,
                });
                that.polyline = new AMap.Polyline({
                    map: that.map,
                    path: that.lineArr,
                    showDir: true,
                    strokeColor: "#28F", //线颜色
                    // strokeOpacity: 1,     //线透明度
                    strokeWeight: 6, //线宽
                    // strokeStyle: "solid"  //线样式
                });
                that.passedPolyline = new AMap.Polyline({
                    map: that.map,
                    // path: lineArr,
                    strokeColor: "#AF5", //线颜色
                    // strokeOpacity: 1,     //线透明度
                    strokeWeight: 6, //线宽
                    // strokeStyle: "solid"  //线样式
                });
                that.marker.on('moving', function(e) {
                    that.passedPolyline.setPath(e.passedPath);
                });
                that.map.setFitView();
            },
            startAnimation() {
                this.marker.moveAlong(this.lineArr, 200);
            },
            pauseAnimation() {
                this.marker.pauseMove();
            },
            resumeAnimation() {
                this.marker.resumeMove();
            }
        },
        mounted() {
            let that = this;
            MapLoader().then(AMap => {
                that.map = new AMap.Map("container", {
                    center: [116.397428, 39.90923],
                    zoom: 17,
                    resizeEnable: true, //是否监控地图容器尺寸变化
                    mapStyle: "amap://styles/0d8c3dd312b132f0ad4d0391a26a45ad"
                });
            });
            setTimeout(() => {
                that.loadFlvPlear();
                that.timer = setInterval(() => {
                    that.updateData();
                }, 1000);
            }, 1000);
            //定时刷新
        },
    };
</script>

<style lang="less" scoped>
    @import "./index.less";
</style>